import { useEffect, useRef, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { ICompanyDetail } from '../../../interfaces';

interface Props {
  className?: string;
  companyDetail: ICompanyDetail;
  hSize: string;
  wSize: string;
  show?: boolean;
}

export default function CompanyThumbCard(props: Props) {
  const { className, show, companyDetail, hSize, wSize } = props;
  const { name, image, id } = companyDetail;
  const [toggleHover, setToggleHover] = useState(false);
  const [offsetTop, setOffsetTop] = useState(0);
  const navigate = useNavigate();
  const idElement = `${Math.random()}_${id}`;

  useEffect(() => {
    const dom = document.getElementById(idElement);
    setOffsetTop(dom?.offsetTop || 0);
  }, []);

  const style = {
    top: offsetTop < 206 ? '120%' : '0',
    bottom: offsetTop < 206 ? '0' : '120%',
  };

  return (
    <div
      id={idElement}
      onMouseEnter={() => setToggleHover(true)}
      onMouseLeave={() => setToggleHover(false)}
      className={`relative flex-shrink-0 border border-white/20 bg-white/20 hover:bg-white/50 ${
        className || ''
      }`}
      style={{ width: wSize, height: hSize }}
    >
      {toggleHover && (
        <div
          className="absolute w-36 overflow-hidden rounded-lg bg-white"
          style={{ bottom: '100%' }}
        >
          <div className="p-1">
            <img src={image} alt={name} className="w-full border" />
          </div>
          <div className="p-2">
            <p className="font-primary text-sm">{name}</p>
          </div>
        </div>
      )}

      <button type="button" onClick={() => navigate(`/booth/${id}`)}>
        <img
          src={image}
          className="block h-full w-full object-cover opacity-0"
          alt={name}
        />
      </button>
    </div>
  );
}
