import { useContext, useRef } from 'react';
import { Helmet } from 'react-helmet';
import { AppContext } from '../../../context/AppContext';
import { titlePageGenerator } from '../../../utils';
import Menu3D from '../../organisms/Menu3D/Menu3D';
import Navigation3D from '../../organisms/Navigation3D/Navigation3D';
import image from './entre-expo.png';
import {
  entreExpoDataLine1,
  entreExpoDataLine2,
  entreExpoDataLine3,
} from '../../../data/entre-expo';
import CompanyThumbCard from '../ExpoHall/CompanyThumbCard';
import TransitionLayer from '../../atoms/TransitionLayer/TransitionLayer';
import useOnLoadImages from '../../../hooks/useOnLoadImages';

export default function EntrepreneurshipExpo() {
  const ctx = useContext(AppContext);
  const wrapperRef = useRef<HTMLDivElement>(null);
  const imagesLoaded = useOnLoadImages(wrapperRef);

  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('Expo Hall')}</title>
      </Helmet>
      {ctx.showMenu3D && <Menu3D />}
      <div className="relative h-screen overflow-hidden" ref={wrapperRef}>
        <img
          src={image}
          alt=""
          className="absolute top-0 left-0 -z-10 h-screen w-full object-cover shadow-lg"
        />
        <Navigation3D href="/lobby" />
        <div className="flex h-screen w-full flex-col items-center">
          {/* divider */}
          <div className="h-[46.35vh]" />

          <div className="-ml-3 flex h-[5vh] w-[49vw] justify-start gap-[1.15vw] bg-purple-500/0">
            {entreExpoDataLine1.map((item, index) => (
              <CompanyThumbCard
                companyDetail={item}
                hSize="5vh"
                wSize="3vw"
                className="bg-primary/20"
              />
            ))}
          </div>
          <div className="h-[4.55vh]" />

          <div className="-ml-3 flex h-[5.6vh] w-[53vw] justify-start gap-[1.15vw] bg-purple-500/0">
            {entreExpoDataLine1.map((item, index) => (
              <CompanyThumbCard
                companyDetail={item}
                hSize="5.6vh"
                wSize="3.35vw"
                className="bg-primary/20"
              />
            ))}
          </div>

          <div className="h-[5vh]" />

          <div className="-ml-3 flex h-[6.5vh] w-[58vw] justify-start gap-[1.15vw] bg-purple-500/0">
            {entreExpoDataLine1.map((item, index) => (
              <CompanyThumbCard
                companyDetail={item}
                hSize="6.5vh"
                wSize="3.75vw"
                className="bg-primary/20"
              />
            ))}
          </div>

          <div className="h-[5.8vh]" />

          <div className="-ml-3 flex h-[7.6vh] w-[63vw] justify-start gap-[1.15vw] bg-purple-500/0">
            {entreExpoDataLine1.map((item, index) => (
              <CompanyThumbCard
                companyDetail={item}
                hSize="7.6vh"
                wSize="4.18vw"
                className="bg-primary/20"
              />
            ))}
          </div>
        </div>
      </div>
      <TransitionLayer showTransition={imagesLoaded} />
    </>
  );
}
